;(function () {
  function heandlerEmot(count, path, result) {
    while (count) {
      result.push(path + count-- + '.gif')
    }
    return result
  }
  let SitichCount = 37

  const Sitich = heandlerEmot(SitichCount, 'https://discuss.js.org/img/emot/Sitich/', [])

  const emot = Sitich[Math.floor(Math.random() * Sitich.length)]
  const style = document.createElement('style')
  style.textContent = `#Discuss .D-input-content {
                            background-repeat: no-repeat;
                            background-position: right bottom 20px;
                            background-image: url(${emot});
                        }`
  document.head.appendChild(style)
})()
